header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: white;
    border-bottom: 1px solid white;
    height: 60px;
}

.user {
    display: flex;
    align-items: center;
    gap: 10px;
}

.logout {
    display: flex;
    align-items: center;
    gap: 10px;
}

header img {
    width: 20px;
    height: 20px;
}

@font-face {
    font-family: Poppins;
    src: url(./fonts/Poppins-Bold.ttf);
}

header p {
    font-family: Poppins;
    font-size: 16px;
}